.center {
  justify-content: center;
  display: flex;
}

.timer {
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 30px;
}

.label {
  text-transform: uppercase;
  font-weight: 500;
}

.border {
  border: 2px solid #a34d4d;
  border-radius: 10px;
  width: 200px;
  height: 50px;
  padding: 20px;
}

.change, #datetime-picker:hover {
  border-color: #1b7ee0;
}

#datetime-picker:focus {
  outline-color: #1b7ee0;
}

button {
  color: #1e90ff;
  cursor: pointer;
  background-color: #b9ddff;
  border: 2px solid #1e90ff;
  border-radius: 10px;
  padding: 15px 28px;
  font-weight: 700;
}

button:hover {
  background-color: #9dcfff;
}

button:disabled {
  color: brown;
  cursor: no-drop;
  background-color: #f08080;
  border-color: #a34d4d;
}
